.Logo {
  --size: 30rem;
  --gap: 1rem;
  --top-scale: .57735;
  --gap-scale: .86603;
  --side-scale: 1.1547;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  display: flex;
  position: sticky;
  inset-block-start: 0;
  box-shadow: 0 1.5rem 1.5rem #fff;
}

.Logo .top {
  width: var(--size);
  height: var(--size);
  background-position: 21.685%;
  background-repeat: no-repeat;
  background-size: cover;
  margin-block-start: calc((var(--top-scale)  - 1) * var(--size));
  margin-block-end: calc(var(--gap-scale) * var(--gap));
}

.Logo .top.flag-pride {
  background-image: url("flag-pride.3e530828.svg");
}

.Logo .top.flag-progress-1 {
  background-image: url("flag-progress-1.6ec431c7.svg");
}

.Logo .top.flag-progress-2 {
  background-image: url("flag-progress-2.268dacbb.svg");
}

.Logo .top.angle-horizontal {
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  transform: scaleY(var(--top-scale));
  transform-origin: bottom;
}

.Logo .top.angle-diagonal {
  transform-origin: 0 100%;
  margin-inline-start: var(--size);
  transform: scale(.707107, .408248)rotate(-45deg);
}

.Logo .sides {
  --bg-color: white;
  --text-color: var(--azul-caixa);
  font-size: calc(.3 * var(--size));
  color: var(--text-color);
  justify-content: center;
  gap: var(--gap);
  font-weight: bold;
  display: flex;
}

.Logo .sides > * {
  width: calc(var(--size) / 2);
  height: calc(var(--side-scale) * var(--size) / 2);
  align-items: center;
  display: flex;
}

.Logo .sides .left {
  transform-origin: 100% 100%;
  transform: skewY(30deg);
}

.Logo .sides .right {
  transform-origin: 0 100%;
  transform: skewY(-30deg);
}

.Logo .sides .text {
  text-align: center;
  width: 100%;
}

.Logo .sides.stripes-1 .left, .Logo .sides.stripes-1 .right, .Logo .sides.stripes-2 .left, .Logo .sides.stripes-2 .right, .Logo .sides.stripes-3 .left, .Logo .sides.stripes-3 .right {
  border-block: calc(.12 * var(--size)) solid var(--text-color);
  background: var(--bg-color);
}

.Logo .sides.stripes-1 .left {
  border-block-end-color: var(--laranja-caixa);
}

.Logo .sides.stripes-1 .right {
  border-block-start-color: var(--laranja-caixa);
}

.Logo .sides.stripes-2 .right {
  border-block-color: var(--laranja-caixa);
  color: var(--laranja-caixa);
}

.Logo .sides.stripes-3 .left, .Logo .sides.stripes-3 .right {
  border-block-end-color: var(--laranja-caixa);
}

.Logo .sides.solid-bg, .Logo .sides.no-text {
  color: var(--bg-color);
}

.Logo .sides.solid-bg .left, .Logo .sides.no-text .left {
  background: var(--text-color);
}

.Logo .sides.solid-bg .right, .Logo .sides.no-text .right {
  background-color: var(--laranja-caixa);
}

.Logo .sides.no-text .text {
  display: none;
}

.Logo .sides.ff-eras {
  font-family: Eras Bold;
  font-size: calc(.32 * var(--size));
}

.Logo.bg-black {
  background: #000;
}

.Logo.bg-blue {
  background: var(--azul-caixa);
}

.Logo.bg-blue .sides {
  --bg-color: var(--azul-caixa);
  --text-color: white;
}

.Logo.bg-white {
  background: #fff;
}

.Logo.filter-grayscale {
  filter: grayscale();
}

@media (width >= 520px) and (height >= 600px) {
  .Logo {
    --size: 50rem;
  }
}

.Options {
  max-width: 40ch;
  margin: 2rem auto;
  padding-inline: 1rem;
}

.Options .field {
  align-items: baseline;
  gap: 1rem;
  width: 100%;
  margin-block: 1rem;
  font-size: 1.8rem;
  display: flex;
}

.Options .field label {
  text-align: right;
  min-width: 12rem;
}

.Options .field select {
  font-family: inherit;
  font-size: inherit;
  background: #fff;
  border: .1rem solid gray;
  border-radius: 1rem;
  flex-grow: 1;
  padding: .5rem 1rem;
}
